import React from 'react';
import ReactDOM from 'react-dom/client';
import OptimolePricingRedesign from './components/OptimolePricingRedesign';
import HomePage from './components/HomePage';
import OptimoleAboutPage from './components/AboutPage';
import ContactPage from './components/ContactPage';
import WordPressPluginPage from './components/WordPressPluginPage';
import DAMPage from './components/DAMPage';
import ComparisonPage from './components/ComparisonPage';
import './index.css';

// Get template data from WordPress
const templateData = window.optimoleTemplatesData || {};
const templateType = templateData.templateType || '';
const isShortcode = templateData.shortcode || false;

// Function to render the component
const renderComponent = (elementId, Component) => {
  const rootElement = document.getElementById(elementId);
  if (rootElement) {
    try {
      ReactDOM.createRoot(rootElement).render(
        <React.StrictMode>
          <Component />
        </React.StrictMode>
      );
      console.log(`Optimole: Successfully rendered ${elementId}`);
    } catch (error) {
      console.error(`Optimole: Error rendering ${elementId}`, error);
      rootElement.innerHTML = `<div class="optimole-error">An error occurred while rendering the component.</div>`;
    }
  }
};

// Initialize when the DOM is ready
document.addEventListener('DOMContentLoaded', () => {
  console.log('Optimole Templates: Initializing React components');
  console.log('Template type:', templateType);
  
  // If we're on a specific template page or rendering a specific shortcode
  if (templateType) {
    switch (templateType) {
      case 'pricing':
        renderComponent('optimole-pricing-root', OptimolePricingRedesign);
        break;
      case 'home':
        renderComponent('optimole-home-root', HomePage);
        break;
      case 'about':
        renderComponent('optimole-about-root', OptimoleAboutPage);
        break;
      case 'contact':
        renderComponent('optimole-contact-root', ContactPage);
        break;
      case 'wordpress':
        renderComponent('optimole-wordpress-root', WordPressPluginPage);
        break;
      case 'dam':
        renderComponent('optimole-dam-root', DAMPage);
        break;
      case 'compare':
        renderComponent('optimole-compare-root', ComparisonPage);
        break;
      default:
        console.warn(`Optimole: Unknown template type "${templateType}"`);
    }
  } else {
    // In case no specific template type is specified, try to render any components found on the page
    // This is useful for pages that might have multiple shortcodes
    const componentMap = {
      'optimole-pricing-root': OptimolePricingRedesign,
      'optimole-home-root': HomePage,
      'optimole-about-root': OptimoleAboutPage,
      'optimole-contact-root': ContactPage,
      'optimole-wordpress-root': WordPressPluginPage,
      'optimole-dam-root': DAMPage,
      'optimole-compare-root': ComparisonPage
    };
    
    Object.entries(componentMap).forEach(([elementId, Component]) => {
      const element = document.getElementById(elementId);
      if (element) {
        renderComponent(elementId, Component);
      }
    });
  }
}); 